
body {
   margin: 0 auto;
   overflow-x: hidden;
   font-family: 'Fjalla One', sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
}

/* nav styles */

.navbar {
  z-index: 100;
  position: sticky;
  top: 0%;
  display: block;
  width: 100vw;
  padding: 10px;
  display: flex;
  align-items: center;
  background-color: rgba(0, 0, 0, 0);
  color: white;
  font-size: 1.25rem;
}

.logo {
  width: 120px;
  height: auto;
  margin-right: 10px;
}

.nav-brand {
  width: 100%;
  cursor: pointer;
}

.nav-links {
  width: 100%;
  margin-left: 30%;
  display: flex;
  justify-content: space-around;
}

.nav-link {
  cursor: pointer;
}

/* hero image and banner text */

.landing {
  display: block;
  z-index: -100;
  position: absolute;
  top: -7%;
  height: 101vh;
  width: 100vw;
}

.hero-banner {
  height: 100%;
  background: url('./assets/bats-banner-1.gif');
  background-size: cover;
  background-repeat: no-repeat;
}

.banner-bg {
  height: 100%;
  background: rgb(5,5,55, .45);
}

.banner-text {
  position: relative;
  top: 30%;
  left: 25%;
  width: 50%;
  text-align: center;
  color: white;
  font-size: 2rem;
}

.subtitle {
  margin-top: -3%;
}

.banner-buttons {
  display: flex;
  justify-content: center;
}

.banner-button {
  background-color: white;
  color: black;
  font-size: 1.5rem;
  border: 1px white solid;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

.banner-button:first-of-type {
  margin-right: 15px;
}

.banner-button:hover {
  background-color: black;
  color: white;
}

/* message */

.message {
  position: relative;
  top: 30.5%;
  display: block;
  height: 75vh;
  width: 100vw;
}

.message-content {
  height: auto;
  margin-top: 1%;
  display: flex;
  justify-content: center;
  align-self: flex-start;
}

.sensei-img {
  width: 40%;
  align-self: center;
  text-align: center;
}

.sensei-img img {
  height: 300px;
  width: 300px;
  border-radius: 50%;
}

.sensei-msg {
  width: 50%;
  height: auto;
  align-self: center;
  font-size: 1.5rem;
}

.sensei-msg a {
  text-decoration: none;
  color: black;
}

.flyer {
  width: 100%;
  align-self: center;
  border: 2px solid black;
  border-radius: 5px;
  padding: 10px 0px 10px 0px;
  text-align: center;
  cursor: pointer;
}

.flyer:hover {
  border: 2px solid white;
  background-color: black;
  color: white;
}

/* about */

.about {
  position: relative;
  top: 40%;
  height: 70vh;
  width: 100vw;
  display: flex;
  flex-direction: row;
  align-self: center;
  align-items: center;
  justify-content: center;
}

.about-img {
  width: 50%;
  height: 100%;
}

.about-img img {
  height: 100%;
  width: auto;
}

.about-info {
  width: 50%;
  height: 100%;
  background-color: #4A4843;
  color: white;
}

.about-cost {
  position: relative;
  left: 5%;
  width: 90%;
  display: flex;
  justify-content: space-between;
  text-align: center;
  padding: 0px 0px 0px 0px;
}

.hidden {
  visibility: hidden;
}

.fade-in-up {
  animation: fadeInUp .5s ease-in;
}

@keyframes fadeInUp {
    from {
        transform: translate3d(0,40px,0);
        opacity: 0
    }

    to {
        transform: translate3d(0,0,0);
        opacity: 1
    }
}

@-webkit-keyframes fadeInUp {
    from {
        transform: translate3d(0,40px,0);

    }

    to {
        transform: translate3d(0,0,0);
        opacity: 1
    }
}

.cost-info {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.about-styles {
  width: 100%;
  display: flex;
  align-self: center;
  align-content: center;
  justify-content: space-around;
  margin-top: 10%;
}

.style-icon {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.5rem;
}

.style-icon img {
  width: 125px;
  height: 125px;
  margin-bottom: 15px;
  color: white;
}

/* register */

.register {
  position: relative;
  top: 41%;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: center;
}

.register-select {
  display: flex;
  height: 70%;
}

.register-info {
  width: 50%;
  height: 100%;
  background-color: #C3B7BC;
}

.register-info:nth-of-type(2) {
  background-color: #B0A098;
}

.competitor, .volunteer {
  padding-top: 13%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.25rem;
}

button {
  font-family: 'Fjalla One', sans-serif;
  width: 50%;
  margin-top: 20px;
  background-color: white;
  color: black;
  font-size: 1.5rem;
  border: 1px white solid;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: black;
  color: white;
  cursor: not-allowed;
  -webkit-box-shadow: 0px 2px 16px 1px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 2px 16px 1px rgba(0,0,0,0.75);
  box-shadow: 0px 2px 16px 1px rgba(0,0,0,0.75);
}

label {
  display: block;
  font-family: 'Fjalla One', sans-serif;
  font-size: 1.5rem;
  margin-bottom: 3.5%;
}

input {
  position: relative;
  left: 25%;
  display: block;
  width: 50%;
  padding: 1% 0 1% 0;
  font-family: 'Fjalla One', sans-serif;
  font-size: 1.5rem;
  margin-bottom: 3.5%;
}

/* contact */

.contact {
  position: relative;
  top: 35%;
  height: 70vh;
  width: 100vw;
}

.contact-header {
  text-align: center;
}

.contact-info {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-self: center;
  align-items: center;
  justify-content: flex-start;
  background-color: black;
}

.contact-img {
  width: 50%;
  height: 100%;
}

.contact-img img {
  width: 100%;
  height: 100%;
  margin-bottom: -5%;
}

.address-container {
  height: 100%;
  width: 50%;
  background-color: #4A4843;
  color: white;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: space-around;
}

.address {
  font-size: 1.5rem;
}

.contact-social {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.contact-social img {
  height: 65px;
  width: auto;
}

.contact-footer {
  display: flex;
  flex-direction: column;
  text-align: center;
}

/* small phone screens - portrait mode */
@media only screen and (min-width: 200px) and (max-width: 374px) {

  /* nav */
  .navbar {
    display: none;
  }

  /* home */
  .landing {
    top: -1%;
  }

  .hero-banner {
    background-position: center;
  }

  .banner-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 1rem;
    top: 30%;
  }

  .subtitle {
    margin-top: -3%;
  }

  .banner-buttons {
    flex-direction: column;
  }

  .banner-button {
    font-size: 1rem;
    width: 90%;
  }

  .banner-button:nth-of-type(1) {
    margin-bottom: 10px;
  }

  /* message */

  .message {
    top: 33%;
  }
  .message-content {
    flex-direction: column;
  }

  .sensei-img {
    align-self: center;
  }

  .sensei-img img {
    height: 100px;
    width: 100px;
  }

  .sensei-msg {
    width: 80%;
    align-self: center;
    font-size: .75rem;
    text-align: center;
  }


  /* about */

  .about {
    top: 63%;
    flex-direction: column;
  }

  .about-img {
    width: auto;
  }

  .about-img img {
    height: 100%;
    width: auto;
  }

  .about-info {
    width: 100%;
  }

  .about-styles {
    width: 100%;
    margin: 5% 0 5% -2.5%;
  }

  .style-icon {
    font-size: 1.1rem;
  }

  .style-icon img {
    width: 70px;
    height: auto;
  }

  /* register */

  .register {
    top: 73%;
  }

  .register h1 {
    font-size: 1.5rem;
  }

  .register-select {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .register-info {
    width: auto;
    height: auto;
    padding-bottom: 20%;
  }

  .competitor, .volunteer {
    font-size: 1rem;
  }

  button {
    font-size: 1rem;
  }

  /* contact */

  .contact {
    top: 141%;
  }

  .contact-header {
    position: relative;
    top: -133%;
  }

  .contact-info {
    position: relative;
    top: 10%;
    flex-direction: column-reverse;
  }

  .contact-img {
    width: 100%;
    height: auto;
  }

  .contact-img img {
    width: 100%;
    height: auto;
  }

  .address-container {
    height: auto;
    width: 100%;
  }

  .address {
    font-size: 1.5rem;
  }

  .contact-footer {
    margin-top: 5%;
  }

}

